import { ListPage } from './../../pages/list/list';
import { NavController } from 'ionic-angular';
import { Component, Input } from '@angular/core';
import { TimeInterval } from 'rxjs';

/**
 * Generated class for the ImagedButtonComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'imaged-button',
  templateUrl: 'imaged-button.html',
})
export class ImagedButtonComponent {

  text: string;

  @Input() inputData : any
  imageURL : any;
  interval : any;
  styleFromTS : any;
  
  constructor(public navCtlr : NavController) {
    console.log('Hello ImagedButtonComponent Component');
    // console.log(document.getElementById(this.object.name))
    
  }
  
  ngAfterViewInit(){
    this.styleFromTS = {"background-image" : "url(" + this.inputData.url + ")"}
  }

  createElement(object){
    console.log(object)
    if(!object.array && object.page){
      this.navCtlr.push(object.page , { "tittle" : object.tittle , "description" : object.description})
    }else if(object.site){
      this.navCtlr.push(ListPage , { "siteUrl" : object.site , "tittle" : object.name})
    }else{
      this.navCtlr.push(ListPage , { "array" : object.array , "tittle" : object.name})
    }
  }


}



