import { ListPage } from './../../pages/list/list';
import { Component, Input } from '@angular/core';
import { NavController} from 'ionic-angular';


/**
 * Generated class for the MyListComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'my-list',
  templateUrl: 'my-list.html'
})
export class MyListComponent {

  @Input() message: any;
  path : any = "./assets/imgs/estrella.jpeg";

  constructor(public navCtlr : NavController) {


  }



  createElement(object){
    console.log(object)
    if(!object.array && object.page){
      this.navCtlr.push(object.page , { "tittle" : object.tittle , "description" : object.description})
    }else if(object.site){
      this.navCtlr.push(ListPage , { "siteUrl" : object.site , "tittle" : object.name})
    }else{
      this.navCtlr.push(ListPage , { "array" : object.array , "tittle" : object.name})
    } 
  }

}



